<template lang="html">
<div class="page">
  <cube-scroll ref="scroll" :options="{scrollbar:{fade:true}}">

    <div class="flex bg-white line-top p-md">
      <avatar :src="obj.Avatar" class="mr"></avatar>
      <div class="f-body">
        <div class="fs-sm tc-grey">{{obj.NickName}}</div>
        <div class="desc flex jc-sb">
          <p class="left">部门：财务部</p>
          <span>{{ obj.UpdateTime }}</span>
        </div>
      </div>
    </div>
    <tit has-bg class="mt-md">调度信息</tit>
    <div class="info-block">
      <cube-input v-model="name" placeholder="请输入调度名称"></cube-input>
      <cube-textarea v-model="desc" placeholder="请输入详细信息" class="mt-lg" :maxlength="120" clearable></cube-textarea>
      <h4 class="title mt">相关图片</h4>
      <el-row :gutter="10" >
        <el-col :span="8" v-for="(item,index) in List" :key="index+Math.random()" v-if="index<3">
          <g-item :thum-src="item.CoverImage" img-height="60" href="/"></g-item>
        </el-col>
      </el-row>
      <h4 class="title mt">相关视频</h4>
      <el-row :gutter="10" >
        <el-col :span="8" v-for="(item,index) in List" :key="index+Math.random()" v-if="index>4 && index<8">
          <g-item :thum-src="item.CoverImage" img-height="60" is-video href="/"></g-item>
        </el-col>
      </el-row>
    </div>
    <div class="btn-wrap">
      <btn type="primary" block size="lg">提交信息</btn>
    </div>


  </cube-scroll>

</div>
</template>

<script>
export default {

  data() {
    return {
      List: [],
      obj:{},
      name:'',
      desc:''
    }
  },
  created () {
    this.getListData()
  },
  methods : {
    getListData() {
      this.id = this.$route.query.id
      console.log(this.$route.query.id2);
      this.axios.get('/static/data.json').then((res) => {
        this.List = res.data.List
        this.obj = res.data.List[this.id]
      })
    },


  },
}
</script>

<style lang="stylus">
@import "assets/stylus/base"

</style>



